body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

input {
    border-top-style: hidden;
    border-right-style: hidden;
    border-left-style: hidden;
    border-bottom-style: hidden;
    background-color: #353535;
    border-radius: 5px;
    color: #EEEEEE;
    height: 2.5rem;
}

button {
    border-top-style: hidden;
    border-right-style: hidden;
    border-left-style: hidden;
    border-bottom-style: hidden;
    height: 2.5rem;
    width: 6.5rem;
    border-radius: 5px;
    background-color: #2F65A7;
    color: #EEEEEE;
}

button:hover {
    background-color: #2f55a7;
    transition: 0.2s;
}

input:focus {
    outline: none;
}


.input-container {
    display: flex;
    padding: 5px;
}

.input-field {
    padding-left: 20px;
    outline: none;
    flex-grow: 1;

}

.input-field-wrapper {
    padding-right: 10px;
    flex-grow: 1;
    display: flex;
}
.textbox {
    display: flex;
    flex-direction: column;
}

.photo-content-container {
    display: flex;
}

.right-container, .left-container  {
    border-radius: 10px;
    padding: 0.5em;
    height: auto;
}


.left-container {
    background-color: #023047;
}

.right-container {
    background-color: #ffb703;
}

.left-textbox-content {
    color: #EEEEEE;
}

.right-textbox-content {
    color: #252323;
}

.right-textbox-photo, .left-textbox-photo {
    border-radius: 50%;
    max-width: 3em;
    height: auto;
    align-items: flex-end;
    padding-right: 5px;
}

.right-textbox-photo {
    display: none;
}

.right-textbox-name  {
    display: none;
}



.left-textbox-name {
    padding-bottom: 5px;
    color: #EEEEEE;
}







.message-container {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-height: 95%;
    overflow-y: auto;
    position: absolute;
    padding: 5px;
}




.right-message {
    margin-left: auto;
    padding-right: 2.5rem;
    padding-bottom: 2px;
}



.left-message {
    margin-right: auto;
}
.sidebar {
    flex-direction: column;
    display: flex;
    width: 14em;
    height: 100vh;
    padding-top: 5px; 
    background-color: #353535;
    align-items: center;    

}
.google_button {
   background-color: white;
   color: black;
   border: none;
   height: 2rem;
   width: 6rem;
   text-decoration: none;
   text-align: center;
   border-radius: 5px;
   font-size: 14px;
   font-weight: normal;
   font-family: 'Open Sans', sans-serif;
   margin-left: 5px;
   margin-top: 10px;
}


.google_button:hover {
    background-color: black;
    color: #EEEEEE;
    transition: 0.2s;
}


    

#btn_content {
    position: fixed;
}
#icon {
    padding-right: 6px;
}

#button_content {
    display: flex;
    justify-content: center;
    
}
  
  
h1 {
    margin: 0;
    color: #EEEEEE;
}




.App {
    display: flex;
    min-height: 100vh;
}

.content-wrap {
    flex: 1 1;
    display: flex;
    flex-direction: column;
}

.content-chatbar {
    display: flex;
    flex-direction: column;
    flex: 1 1
}


.header {
    background-color: #424242;
    padding: 1rem;
}

.messages-container {
    background-color: #424242;
    border-top: 1px solid #FFD369;
    border-bottom: 1px solid #FFD369;
    height: 100%;
    padding: 1rem;
    overflow: hidden;
    position: relative;
    
  
}

.send-message-container {
    background-color: #424242;
    padding: 1.4rem;
}


